import React from "react"
import style from "../styles/itSkills.module.css"

const ITSkills = () => {
    return (
        <section className={style.container}>
            <br></br>
        <h4 style={{textAlign:'center', marginBottom:'0.8', padding:'0 1rem 0'}}>Technology Skills</h4>
            <div className={style.box}>
                <div className={style.insidebox}>
                    <p className={style.title}>Programming</p>
                    <p>TypeScript, JavaScript, </p>
                    <p>HTML, CSS, Python,</p>
                    <p>MySQL, NoSQL, GraphQL,</p>
                    <p>Java, C++, PHP</p>
                </div>
            </div>

            {/* <div className={style.box}>
            <div className={style.insidebox}>
                <p className={style.title}>Database</p>
                <p>MySQL,</p>
                <p>NoSQL</p>
                </div>
            </div>
            <div className={style.box}>
            <div className={style.insidebox}>
                <p className={style.title}>Testing</p>
                <p>JavaScript (Jasmine, Jest),</p>
                <p> Travis CI</p>
                </div>
            </div> */}
            <div className={style.box}>
            <div className={style.insidebox}>
                <p className={style.title}>Cloud Services / Tools</p>
                <p>Amazon Web Services,</p>
                <p> MongoDB Atlas,</p>
                <p>Travis CI,</p>
                <p>Git (github, Bitbucket)</p>
                </div>
            </div>
            {/* <div className={style.box}>
            <div className={style.insidebox}>
                <p className={style.title}>Version Control</p>
                <p>Git (github, Bitbucket)</p>
                </div>
            </div> */}
            <h4 style={{ textAlign:'center', marginBottom:'0.8', padding:'0 1rem 0'}}>Spoken Languages</h4>
            <div className={style.smallbox}>
            <p className={style.title}>English</p>
            </div>
            <div className={style.smallbox}>
            <p className={style.title}>French</p>
            </div>
            <div className={style.smallbox}>
            <p className={style.title}>Mandarin</p>
            </div>
        </section>
    )
}

export default ITSkills